
import { useStore } from '@/store'
import { defineComponent, computed, ref, watch } from 'vue'
import LixinTable from '@/base-ui/table'
import { handleStopAction } from '@/service/main/system/system'

export default defineComponent({
  components: {
    LixinTable
  },
  props: {
    contentTableConfig: {
      type: Object,
      required: true
    },
    pageName: {
      type: String,
      required: true
    }
  },
  // methods: {
  //   formatTime(time: any) {
  //     console.log(time)
  //     return dayjs.utc(time).format('YYYY-MM-DD HH:mm:ss')
  //   }
  // },
  emits: ['createBtnClick', 'updateBtnClick'],
  setup(props, { emit }) {
    const store = useStore()

    // 绑定Pageinfo
    const pageInfo = ref({ currentPage: 1, pageSize: 10 })

    watch(pageInfo, () => getPageData())
    // 发送网络请求
    const getPageData = (queryInfo: any = {}) => {
      store.dispatch('system/getPageListAction', {
        pageName: props.pageName,
        queryInfo: {
          offset: (pageInfo.value.currentPage - 1) * pageInfo.value.pageSize,
          limit: pageInfo.value.pageSize,
          ...queryInfo
        }
      })
    }
    getPageData()
    // 获取数据
    const dataList = computed(() =>
      store.getters[`system/pageListData`](props.pageName)
    )
    const listCount = computed(() =>
      store.getters[`system/pageListCount`](props.pageName)
    )

    // 4. 获取其他动态插槽
    const otherPropSlots = props.contentTableConfig?.propList.filter(
      (item: any) => {
        if (item.slotName === 'status') return false
        if (item.slotName === 'createAt') return false
        if (item.slotName === 'updateAt') return false
        if (item.slotName === 'iconURL') return false
        if (item.slotName === 'avatarUrl') return false
        if (item.slotName === 'stopAt') return false
        if (item.slotName === 'handler') return false
        return true
      }
    )
    // 5. 删除/新建/编辑操作
    const handleDeleteClick = (item: any) => {
      let ID = item.id
      if (props.pageName === 'topic') {
        ID = item.tid
      }
      store.dispatch('system/deletePageDataAction', {
        pageName: props.pageName,
        id: ID
      })
    }

    const handleCreateClick = () => {
      emit('createBtnClick')
    }
    const handleUpdateClick = (item: any) => {
      emit('updateBtnClick', item)
    }

    const handleYesWinClick = (item: any) => {
      handleStopAction('topic/stop', {
        tid: item.tid,
        yes_win: true,
        no_win: false
      })
    }
    const handleNoWinClick = (item: any) => {
      handleStopAction('topic/stop', {
        tid: item.tid,
        yes_win: false,
        no_win: true
      })
    }

    // 6. 时间序列化

    return {
      dataList,
      listCount,
      getPageData,
      pageInfo,
      otherPropSlots,
      handleDeleteClick,
      handleCreateClick,
      handleUpdateClick,
      handleYesWinClick,
      handleNoWinClick
    }
  }
})
