import pageModel from '@/components/page-model'
import { ref } from 'vue'
type CallbackTypeFn = () => void

export function usePageModel(
  createCb?: CallbackTypeFn,
  updateCb?: CallbackTypeFn
) {
  const pageModelRef = ref<InstanceType<typeof pageModel>>()
  const defaultInfo = ref({})
  const handleCreateData = () => {
    defaultInfo.value = {}
    /* eslint-disable */
    pageModelRef!.value!.dialogVisible = true
    createCb && createCb()
  }
  const handleUpdateData = (item: any) => {
    defaultInfo.value = {...item}
    pageModelRef!.value!.dialogVisible = true
    updateCb && updateCb()
  }
  return [pageModelRef, handleCreateData, handleUpdateData,defaultInfo]
}
